@import url("https://fonts.googleapis.com/css2?family=Baloo+Paaji+2&family=Bangers&family=Caveat&family=Fira+Sans+Condensed:wght@100&family=Quintessential&family=Syne+Tactile&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  height: 100vh;
  width: 100%;
}
.logoHeader {
  height: 80px;
  width: 100%;
  background-color: rgb(233, 224, 224);
  display: flex;
  justify-content: center;
  align-items: center;
}
.sontechImage {
  height: 250px;
  width: 180px;
}
.noteKeeper {
  font-size: 25px;
  font-family: "Syne Tactile", cursive;
  margin-left: 10px;
}
.footer {
  width: 100%;
  height: 35px;
  background-color: black;
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  /* margin-top: auto; */
}
.noteWrite {
  width: 27%;
  box-shadow: 4px 8px 8px 0px rgb(214, 211, 211);
  margin: auto;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}
.title {
  width: 100%;
  outline: none;
  margin-top: 2px;
  height: 25px;
  border: none;
  font-size: 17px;
  padding: 10px;
}
.noteTextarea {
  width: 100%;
  outline: none;
  margin-top: 10px;
  font-size: 17px;
  border: none;
  padding: 10px;
}
.addButton {
  width: 100%;
}
.noteShow {
  box-shadow: 3px 5px 8px 1px rgb(184, 182, 182);
  width: 15%;
  padding: 5px;
  margin: 20px 0;
  display: inline-block;
}
.deleteButton {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  margin-left: auto;
  margin-top: 10px;
  cursor: pointer;
  border: none;
  transition: all 0.7s ease;
}
.deleteButton:hover {
  background-color: red;
  color: #ffffff;
}
@media (max-width: 992px) {
  .noteWrite {
    width: 100%;
  }
  .noteShow {
    width: 95%;
  }
}
